import { useRoketoContext } from '@app/app/roketo-context';
import { env } from '@app/shared/config';
import { useToken } from '@app/shared/hooks/useToken';

type BalanceProps = {
  tokenAccountId: string,
  className?: string,
}

export function Balance({ tokenAccountId, className }: BalanceProps) {
  const { auth } = useRoketoContext()
  const { balance, formatter, meta } = useToken(tokenAccountId);

  // tmp: hard code for wNear
  const actualBalance = tokenAccountId === env.WNEAR_ID
    ? auth.balance?.available || '0'
    : balance;

  return (
    <span className={className}>
      Balance:
      {' '}
      {formatter.amount(actualBalance)}
      {' '}
      {meta.symbol}
    </span>
  );
}
